___CSS_LOADER_EXPORT___.push([module.id, `body{
    display: grid;
    font-family: 'Open Sans', sans-serif;
}
#title{
    justify-self: center;
    font-weight: 400;
    font-size: 4rem;
}
#search{
    justify-self: center;
    display: flex;
    gap: 1rem;
}
#location{
    width: 12rem;
    height: 1.2rem;
}
#go{
    width: 2.5rem;
}
#weatherCardContainer{
    justify-self: center;
    margin-top: 3rem;
    width: 1000px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
}
#statementContainer{
    font-size: 2.5rem;
    font-weight: 300;
    flex: 1 0 100%;
    order: 0;
    display: flex;
    justify-content: center;
}
#currentTempsContainer{
    margin: 0 4rem;
    flex: 0;
    order: 2;
}
#current-temp{
    font-size: 6rem;
    margin: 1rem 0 0 0;
    justify-self: center;
    align-self: start;
}
#hiLow{
    justify-self: center;
    margin-right: 2rem;
}
.conditions-icon{
    width: 10rem;
    height: 10rem;
    margin: 0 4rem;
    flex: 0;
    order: 1;
    align-self: center;
}
#futureTempsContainer{
    order: 3;
    display: flex;
    gap: 10rem;
    margin: 3rem 10rem 0rem 10rem;
}
.dayOfWeek{
    font-size: larger;
    font-weight: bold;
}
.futureHiLow{
    justify-self: center;
    margin: 0;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,oCAAoC;AACxC;AACA;IACI,oBAAoB;IACpB,gBAAgB;IAChB,eAAe;AACnB;AACA;IACI,oBAAoB;IACpB,aAAa;IACb,SAAS;AACb;AACA;IACI,YAAY;IACZ,cAAc;AAClB;AACA;IACI,aAAa;AACjB;AACA;IACI,oBAAoB;IACpB,gBAAgB;IAChB,aAAa;IACb,aAAa;IACb,eAAe;IACf,uBAAuB;IACvB,SAAS;AACb;AACA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,cAAc;IACd,QAAQ;IACR,aAAa;IACb,uBAAuB;AAC3B;AACA;IACI,cAAc;IACd,OAAO;IACP,QAAQ;AACZ;AACA;IACI,eAAe;IACf,kBAAkB;IAClB,oBAAoB;IACpB,iBAAiB;AACrB;AACA;IACI,oBAAoB;IACpB,kBAAkB;AACtB;AACA;IACI,YAAY;IACZ,aAAa;IACb,cAAc;IACd,OAAO;IACP,QAAQ;IACR,kBAAkB;AACtB;AACA;IACI,QAAQ;IACR,aAAa;IACb,UAAU;IACV,6BAA6B;AACjC;AACA;IACI,iBAAiB;IACjB,iBAAiB;AACrB;AACA;IACI,oBAAoB;IACpB,SAAS;AACb","sourcesContent":["body{\n    display: grid;\n    font-family: 'Open Sans', sans-serif;\n}\n#title{\n    justify-self: center;\n    font-weight: 400;\n    font-size: 4rem;\n}\n#search{\n    justify-self: center;\n    display: flex;\n    gap: 1rem;\n}\n#location{\n    width: 12rem;\n    height: 1.2rem;\n}\n#go{\n    width: 2.5rem;\n}\n#weatherCardContainer{\n    justify-self: center;\n    margin-top: 3rem;\n    width: 1000px;\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    gap: 1rem;\n}\n#statementContainer{\n    font-size: 2.5rem;\n    font-weight: 300;\n    flex: 1 0 100%;\n    order: 0;\n    display: flex;\n    justify-content: center;\n}\n#currentTempsContainer{\n    margin: 0 4rem;\n    flex: 0;\n    order: 2;\n}\n#current-temp{\n    font-size: 6rem;\n    margin: 1rem 0 0 0;\n    justify-self: center;\n    align-self: start;\n}\n#hiLow{\n    justify-self: center;\n    margin-right: 2rem;\n}\n.conditions-icon{\n    width: 10rem;\n    height: 10rem;\n    margin: 0 4rem;\n    flex: 0;\n    order: 1;\n    align-self: center;\n}\n#futureTempsContainer{\n    order: 3;\n    display: flex;\n    gap: 10rem;\n    margin: 3rem 10rem 0rem 10rem;\n}\n.dayOfWeek{\n    font-size: larger;\n    font-weight: